const Dracula = require('graphdracula');
const genarateNodes = require('./node-genarator');

const g = new Dracula.Graph();

const nodes = genarateNodes(10);


const layouter = new Dracula.Layout.Spring(g);
layouter.layout();

const renderer = new Dracula.Renderer.Raphael('#canvas', g, 400, 300);
renderer.draw();